/* Style global */
body {
  font-family: 'Arial', sans-serif;
  background-color: #121212; /* Fond sombre */
  color: #e0e0e0; /* Texte clair */
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: flex-start; /* Pour que le contenu ne soit pas centré sur la page */
  min-height: 100vh; /* Assure que la hauteur du body est toujours au moins égale à la hauteur de l'écran */
  overflow-x: hidden; /* Empêche le défilement horizontal */
  overflow-y: auto; /* Permet le défilement vertical */
}

/* Conteneur principal */
.container {
  width: 90%;
  max-width: 800px;
  text-align: center;
  background-color: #1f1f1f; /* Fond légèrement plus clair */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
  margin: 15px auto;
}

/* Entrée de texte et bouton de recherche */
input {
  width: 85%;
  max-width: 400px;
  padding: 10px;
  margin-top: 20px;
  border-radius: 5px;
  border: 1px solid #444;
  background-color: #2c2c2c;
  color: #fff;
  font-size: 16px;
  margin-bottom: 20px;
}

button {
  padding: 10px 20px;
  background-color: #4CAF50; /* Vert pour accent */
  border: none;
  color: white;
  font-size: 16px;
  margin-top: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #45a049;
}

/* Résultats de la météo actuelle */
#weatherResult {
  background-color: #2c2c2c;
  padding: 20px;
  margin-top: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
}

#weatherResult h2 {
  color: #d1d1d1;
}

#weatherResult p {
  font-size: 18px;
  margin: 5px 0;
}

/* Prévisions sur 5 jours */
#forecastResult {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 15px;
  margin-top: 30px;
}

.forecast-day {
  background-color: #333;
  padding: 15px;
  border-radius: 8px;
  color: #fff;
  text-align: center;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
}

.forecast-day img {
  width: 50px;
  height: 50px;
}

.forecast-day p {
  margin: 5px 0;
}

.forecast-day p:nth-of-type(2) {
  font-size: 14px;
  color: #aaa;
}

/* Mode clair */
.light-mode {
  background-color: #f0f0f0;
  color: #ececec;
}

/* Mode sombre */
.dark-mode {
  background-color: #121212;
  color: #e0e0e0;
}

/* Styles Responsives */
@media (max-width: 1024px) {
  /* Pour les tablettes */
  .container {
    width: 95%;
    padding: 15px;
  }

  #forecastResult {
    grid-template-columns: repeat(3, 1fr);
  }

  .forecast-day img {
    width: 40px;
    height: 40px;
  }
}

@media (max-width: 768px) {
  /* Pour les petits écrans (tablettes portrait et smartphones) */
  .container {
    width: 90%;
    padding: 10px;
  }

  #forecastResult {
    grid-template-columns: repeat(2, 1fr);
  }

  input {
    width: 85%;
    max-width: none;
  }

  button {
    width: 100%;
  }

  .forecast-day img {
    width: 35px;
    height: 35px;
  }

  #weatherResult p {
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  /* Pour les très petits écrans (smartphones en mode portrait) */
  .container {
    width: 100%;
    padding: 10px;
  }

  #forecastResult {
    grid-template-columns: 1fr;
  }

  .forecast-day img {
    width: 30px;
    height: 30px;
  }

  #weatherResult p {
    font-size: 14px;
  }

  h2 {
    font-size: 1.5rem;
  }
}
